.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.4);
  z-index: 100;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.modal {
  position: fixed;
  top: -100%;
  width: 500px;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 16px;
  background-color: white;
  border-radius: 24px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 3px 3px 8px rgba(0,0,0,.6);
          box-shadow: 3px 3px 8px rgba(0,0,0,.6);
  z-index: 500;
  overflow: hidden;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  visibility: hidden;
  opacity: 0;
}

.show {
  visibility: visible;
  opacity: 1;
}

.modal.show{
  top: 50%;
}

.modal button {
  width: 100px;
  color: white;
}

.totalPrice {
  font-size: 1.2rem;
  font-weight: bold;
  color: #b00;
}

.modal h2 {
  margin: 0;
}

.modal table {
  width: 60%;
  margin: 0 auto;
  margin-bottom: 24px;
  padding: 0;
  border-collapse: collapse;
}

.modal tr {
  border-bottom: 1px solid #ccc;
}

.modal th, .modal td {
  padding: 4px 0;
}

@media (max-width: 576px) {
  .modal {
    width: 80%;
  }
  .modal table {
    width: auto;
  }
}


