.allController{
  width: 500px;
  background-color: #ddd;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-shadow: 5px 5px 15px #7f7f7f;
          box-shadow: 5px 5px 15px #7f7f7f;
  margin: 16px auto;
  padding: 12px;
}
@media (max-width: 576px){
  .allController{ 
    width: 100%;
  }
}

.order {
  padding: 8px 24px;
  margin: 12px auto;
  background-color: #e27b36;
  font-family: inherit;
  font-size: 1.2em;
  border: none;
  border-radius: 16px;
  outline: none;
  -webkit-box-shadow: 5px 5px 0 #c15711;
          box-shadow: 5px 5px 0 #c15711;
  cursor: pointer;
}

.order:hover:not(:disabled){
  -webkit-animation: order .3s both ease-out infinite alternate;
          animation: order .3s both ease-out infinite alternate;
}

.order:disabled {
  color: white;
  background-color: #818080;
  -webkit-box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.6);
          box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.6);
  cursor: not-allowed;
}

@-webkit-keyframes order{
  0%{ -webkit-transform: translateY(3px); transform: translateY(3px) }
  100%{ -webkit-transform: translateY(-3px); transform: translateY(-3px) }
}

@keyframes order{
  0%{ -webkit-transform: translateY(3px); transform: translateY(3px) }
  100%{ -webkit-transform: translateY(-3px); transform: translateY(-3px) }
}




